body {
  margin: 0;
  padding: 0;
}
.wrapper-paper {
  width: 350px;
  height: 90vh;
  margin: 20px auto;
  overflow: hidden;
  position: relative;
}
.tabs {
  width: 100%;
}
.title-grid {
  background: linear-gradient(90deg, #ff3b57, #892168);
}
.title {
  width: 100%;
  height: 40px;
  padding-top: 10px;
  color: white;
}
.play-list {
  width: 100%;
  height: 80%;
  overflow: scroll;
  overflow-x: hidden;
}
.play-list img {
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.music-player-bottom {
  padding: 4px 6px;
  width: 100%;
  height: 20%;
}
.music-player-bottom img {
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.slide {
  position: relative;
  margin-left: 0;
  background: yellow;
}
@media only screen and (max-width: 500px) {
  .wrapper-paper {
    height: 100vh;
  }
}
@media only screen and (max-width: 370px) {
  .wrapper-paper {
    width: 100%;
  }
}
